/*---
 title: Typography
 section: Elements
 ---

 #### Headings

 ```example
 <h1>The quick, brown fox (48px)</h1>
 <h2>The quick, brown fox (40px)</h2>
 <h3>The quick, brown fox (31px)</h3>
 <h4>The quick, brown fox (25px)</h4>
 <h5>The quick, brown fox (20px)</h5>
 <h6>The quick, brown fox (16px)</h6>
 ```

 #### Feature & Body Copy

 ```example
 <p class="o-feature">For the execution of the voyage to the Indies, I did not make use of intelligence, mathematics or maps. (24px)</p>
 <p>After having dispatched a meal, I went ashore, and found no habitation save a single house, and that without an occupant; we had no doubt that the people had fled in terror at our approach, as the house was completely furnished. (16px)</p>
 ```

 #### Lists

 ```example
 <ol>
   <li>This is how an ordered list looks.</li>
   <li>I am struggling to say anything more about them.</li>
   <li>That brings us to the next type of list&hellip;</li>
 </ol>

 <ul>
   <li>This is how an unordered list looks.</li>
   <li>I am struggling to say anything more about them.</li>
   <li>That brings us to tiny copy&hellip;</li>
 </ul>
 ```

 ### Customize

 ``` css
  --font-family: 'Source Sans Pro', sans-serif;
  --font-weight: 400;
  --font-weight-bold: 600;
  --h-margin: 1em 0 .25em;
  ```

 */

:root {
  --font-family: 'Source Sans Pro', sans-serif;
  --font-weight: 400;
  --font-weight-bold: 600;
  --h-margin: 0 0 8px;
}

@custom-selector :--headings h1, h2, h3, h4, h5, h6;

.t-default {
  /* specify the root styles of the document */
  background-color: var(--white);
  box-sizing: border-box;
  color: var(--black);
  cursor: default;
  font: 100%/1.5 sans-serif;
  text-rendering: optimizeLegibility;

  /* specify the background color, font color, and drop shadow of text selections */
  ::selection {
    background-color: var(--highlight-announcement); /* required when declaring ::selection */
    color: #4c2b03;
    text-shadow: none;
  }

  /* specify the text decoration of anchors */
  a { text-decoration: none; }

  /* specify the font family of code elements */
  code,
  kbd,
  pre,
  samp { font-family: monospace, monospace; }

  /* specify the list style of nav lists */
  nav ol,
  nav ul { list-style: none; }

  hr {
    border-top: 2px solid var(--grey-300);
    margin: 2.5em 0;
  }

}

/* Lists */

%no-bullet {
  list-style-image: none;
  list-style-type: none;
  margin-left: 0;
}

%no-bullets {
  list-style: none;
  li { @extend %no-bullet; }
}

%list-horizontal {
  @extend %reset-box;
  clear: both;
}

@define-mixin list-inline $list-inline-margin: 1em {
  @extend %no-bullets;
  margin-bottom: .5em;
  margin-left: .5em;
  margin-top: 0;
  overflow: hidden;
  padding: 0;

  > li {
    display: block;
    float: left;
    margin-left: $list-inline-margin;

    > * { display: block; }
  }
}

/* Links */

@define-mixin link-colors $normal, $hover, $active, $visited, $focus {
  color: $normal;
  &:visited { color: $visited; }
  &:focus { color: $focus; }
  &:hover { color: $hover; }
  &:active { color: $active; }
}

%link-hover {
  text-decoration: none;
  &:hover,
  &:focus { text-decoration: underline; }
}

@define-mixin link-unstyled {
  color: inherit;
  cursor: inherit;
  text-decoration: inherit;
  &:active,
  &:focus {
    outline: none;
  }
}

/* Overflow */

%ellipsis,
.u-ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* Headlines */

%h-base-styles {
  line-height: 1.3;
  margin: var(--h-margin);
}

.t-default {
  font: 16px/2 var(--font-family);
  font-weight: var(--font-weight);

  h1,
  h2,
  h3, {
    @extend %h-base-styles;
    font-weight: var(--font-weight);
  }

  h4,
  h5,
  h6 {
    @extend %h-base-styles;
    font-weight: var(--font-weight-bold);
  }

  h1 { font-size: 48px; }
  h2 { font-size: 40px; }
  h3 { font-size: 31px; }
  h4 { font-size: 25px; }
  h5 { font-size: 20px; }
  h6 { font-size: 16px; }

  a:not([class]) {
    @extend %link-hover;
    color: var(--blue-700);
    text-decoration: none;
  }

  p {
    line-height: 1.4;
    margin-bottom: 1em;
  }

  .o-feature { font-size: 24px; }

  small {
    display: block;
    font-size: 75%;
    line-height: 1.5;
    margin-bottom: 1em;
  }
}

