/*---
 section: Form
 title: Inputs
 ---

 Supported input types are, `text`, `password`, `date`, `datetime`, `datetime-local`, `month`, `week`, `email`, `number`, `search`, `tel`, `time`, `url`, `color`.

 ```example:html
  <label id='l1' for="input-1-1">Label</label>
  <input type='text' id="input-1-1" aria-labelledby="l1">
 ```
 ```example:html
  <label id='l2' for="input-1-2">Focus</label>
  <input type='text' id="input-1-2" class="focus" aria-labelledby="l2">
 ```
 ```example:html
  <div class='c-input-group'>
   <label id='l3' for="input-2-1">Warning</label>
   <input type='email' id="input-2-1" value="notauser at clientcom" aria-labelledby="l3" required>
   <div class='c-validation--warning'>Warning validated message</div>
  </div>
 ```
 ```example:html
  <div class='c-input-group'>
   <label id='l4' for="input-2-2">Error</label>
   <input type='text' id="input-2-2" class="error" aria-labelledby="l4">
   <div class='c-validation--error' placeholder="Placeholder text">Error validated message</div>
  </div>
 ```

 ### Textarea
 ```example:html
  <label for='textarea-1-1'>Textarea</label>
  <textarea id='textarea-1-1'></textarea>
 ```

 ### Select
 ```example:html
  <select>
   <option>Option 1</option>
   <option>Option 2</option>
   <option>Option 3</option>
  </select>
 ```

 ### Checkboxes

 This checkbox is rendered based on the Material icon [check box](https://design.google.com/icons/#ic_check_box) and [check box outline blank](https://design.google.com/icons/#ic_check_box_outline_blank).

 ```example:html
  <input id='c1' type="checkbox" class="c-checkbox" aria-labelledby="c1l"></input>
  <label for='c1' id="c1l">Checkbox</label>

  <input id='c2' type="checkbox" class="c-checkbox" aria-labelledby="c2l" checked></input>
  <label for='c2' id="c2l">Checked Checkbox</label>
 ```

 ### Radio

 This radio control is rendered based on the Material icon [radio button checked](https://design.google.com/icons/#ic_radio_button_checked) and the [radio button unchecked](https://design.google.com/icons/#ic_radio_button_unchecked).

 ```example:html
  <input id='r1' type="radio" name="radiogroup" class="c-radio" aria-labelledby="r1l"></input>
  <label for='r1' id="r1l">Radio</label>

  <input id='r2' type="radio" name="radiogroup" class="c-radio" aria-labelledby="r2l" checked></input>
  <label for='r2' id="r2l">Selected Radio</label>
 ```

 */

:root {
  --input-focus-shadow: 0 0 6px var(--blue-600);
  --input-font-size: 18px;
  --input-min-height: 1.5em;
  --textarea-font-size: 16px;
}

@define-mixin validation $color {
  border: 1px solid $color;
  box-shadow: 0 0 6px $color;
  outline: 0;
}

%base-input {
  background-color: var(--white);
  border: 1px solid var(--grey-600);
  border-radius: 4px;
  color: contrast(var(--white));
  display: flex;
  margin: 0 0 16px 0;
  min-height: 38px; /* IE11 Input Height */
  padding: 2px 4px;
  width: 100%;
}

.t-default {
  input {
    border-radius: 0; /* iOS 8+ */

    &[type='button'],
    &[type='reset'],
    &[type='submit'] {
      appearance: button;/* iOS 8+ */
    }

    &[type='number'] {
      width: auto;/* Firefox 36+ */
    }

    &[type='search'] {
      appearance: textfield;/* Chrome 45+, Safari 9+ */

      &::-webkit-search-cancel-button,
      &::-webkit-search-decoration {
        appearance: none;/* Chrome 45+, Safari 9+ */
      }
    }
  }

  select {
    &::-ms-expand {
      @extend %hide; /* Edge 12+, Internet Explorer 11- */
    }

    &::-ms-value {
      color: currentColor; /* Edge 12+, Internet Explorer 11- */
    }
  }

  /* specify the minimum height of form elements */

  button,
  [type='button'],
  [type='date'],
  [type='datetime'],
  [type='datetime-local'],
  [type='email'],
  [type='month'],
  [type='number'],
  [type='password'],
  [type='reset'],
  [type='search'],
  [type='submit'],
  [type='tel'],
  [type='text'],
  [type='time'],
  [type='url'],
  [type='week'],
  select,
  textarea { min-height: var(--input-min-height); }

  input:not([type]),
  [type='text'],
  [type='password'],
  [type='date'],
  [type='datetime'],
  [type='datetime-local'],
  [type='month'],
  [type='week'],
  [type='email'],
  [type='number'],
  [type='search'],
  [type='tel'],
  [type='time'],
  [type='url'],
  [type='color'] {
    @extend %base-input;
    font-size: var(--input-font-size);
    @media var(--md-up) {
      font-size: calc(var(--input-font-size) - 2px);
    }
  }

  textarea {
    @extend %base-input;
    font-size: var(--textarea-font-size);
    height: auto;
    outline: none;
    overflow: auto;
    resize: vertical;
    @media var(--md-up) {
      font-size: calc(var(--textarea-font-size) - 2px);
    }
  }

  input:not([type='range']) {
    transition-duration: var(--global-duration);

    &:focus,
    &.focus {
      @mixin validation var(--blue-600);
    }

    &:focus:invalid,
    &:focus.invalid {
      @mixin validation var(--orange-600);
    }

    &:invalid,
    &.invalid {
      border: var(--orange-600) 1px solid;
    }

    &:focus.error {
      @mixin validation var(--red-600);
    }

    &.error {
      border: var(--red-600) 1px solid;
    }

    & + i {
      background-color: var(--white);
      color: var(--grey-700);
    }

    &:not([aria-label]):not([aria-labelledby]) {
      @extend %assert-selector;
    }

  }

  label {
    color: var(--black);
    cursor: pointer;
    display: block;
    font-size: 14px;
    font-weight: 600;
    line-height: 1.5;
    margin-bottom: 4px;
  }

  /* specify the progress cursor of updating elements */
  [aria-busy='true'] { cursor: progress; }

  /* specify the pointer cursor of trigger elements */
  [aria-controls] { cursor: pointer; }

  /* specify the unstyled cursor of disabled, not-editable, or otherwise inoperable elements */
  [aria-disabled] { cursor: default; }

}

/** @define c-input-group */

.c-input-group {
  position: relative;

  .u-right {
    cursor: pointer;
    padding: 6px 0;
    position: absolute;
    right: 2px;
    text-align: center;
    top: 1px;
    transition-duration: var(--global-duration);
    width: 40px;
  }

}

@define-mixin material-toggle
  $passive-color,
  $passive-icon,
  $active-color,
  $active-icon,
  $margin: .25em {
  height: 0;
  opacity: 0;
  position: absolute;
  width: 0;

  & + label {
    cursor: pointer;
    font-weight: normal;
    line-height: 1em;
    margin-bottom: 1em;
    position: relative;
    text-align: left;
    vertical-align: top;

    &::before,
    &::after {
      direction: ltr;
      display: inline-block;
      font-family: 'Material Icons';
      font-feature-settings: 'liga';
      font-size: 120%;
      font-style: normal;
      font-variant-ligatures: common-ligatures;
      font-weight: normal;
      letter-spacing: normal;
      line-height: 1;
      margin-right: $margin;
      text-transform: none;
      white-space: nowrap;
      word-wrap: normal;
    }

    &::before {
      border-radius: 4px;
      color: $passive-color;
      content: $passive-icon;
    }

    &::after {
      @extend %fade-out;
      color: $active-color;
      content: $active-icon;
      left: 0;
      position: absolute;
    }
  }

  &:checked + label::after { @extend %fade-in; }

  &:focus + label,
  & + label:active,
  &:checked + label:active { box-shadow: var(--input-focus-shadow); }
}

.c-checkbox {
  @mixin material-toggle var(--grey-700), 'check_box_outline_blank', var(--green-600), 'check_box';

  &:indeterminate + label::after {
    color: var(--grey-700);
    content: 'check_box_indeterminate';
  }
}

.c-radio {
  height: 0;
  opacity: 0;
  position: absolute;
  width: 0;

  & + label {
    font-weight: normal;
    position: relative;

    &::before {
      background-color: var(--white);
      border: 2px solid var(--grey-700);
      border-radius: 50px;
      content: '';
      display: inline-block;
      float: left;
      font-size: 14px;
      margin-bottom: 0;
      margin-right: 8px;
      margin-top: 3px;
      padding: 6px;
      position: relative;
    }

    &::after {
      background-color: var(--white);
      border-radius: 50px;
      content: '';
      height: 6px;
      left: 5px;
      opacity: 0;
      position: absolute;
      top: 8px;
      transition-duration: var(--global-duration);
      width: 6px;
      z-index: 5px;
    }
  }

  &:focus + label,
  & + label:active,
  &:checked + label:active {
    box-shadow: var(--input-focus-shadow);
  }

  &:checked + label {
    &::before {
      background-color: var(--green-600);
      border: 2px solid var(--green-600);
      color: #99a1a7;
    }

    &::after {
      opacity: 1;
    }
  }
}

.c-radio--alt {
  @mixin material-toggle var(--grey-700), 'radio_button_unchecked', var(--blue-600), 'radio_button_checked';

  & + label {
    display: inline-block;
    text-align: center !important;

    &::before,
    &::after {
      font-size: 24px !important;
      margin: 0 .125em .25em !important;
    }

    &::before {
      display: block;
    }

    &::after {
      left: auto;
      top: 0;
    }
  }
}

%validation {
  border-radius: 0 0 var(--global-rounded) var(--global-rounded);
  font-size: 12px;
  line-height: 1.3;
  margin-bottom: 16px;
  margin-top: -18px;
  padding: 5px 7px 3px;
  position: relative;
  transition-duration: var(--global-duration);
  user-select: none;
  z-index: -1;
}

.c-validation {
  @extend %validation;

  &--warning {
    @extend %validation;
    background-color: var(--gold-50);
    color: contrast(var(--gold-50));
  }

  &--error {
    @extend %validation;
    background-color: var(--red-600);
    color: contrast(var(--red-600));
  }
}

hr {
  border-top: .5px solid var(--grey-500);
  margin: .5em 0;
}

