/*---
 section: Navigation
 title: Accordion
 ---

 A simple accordion with a gradient of blue. More advanced use cases to come.

 ```example:html
 <nav class="c-accordion">
   <ul class="c-accordion__top">
     <li class="has-descendants">
       <input type="checkbox" id="descendants-1" aria-labelledby="dl1">
       <label for="descendants-1" id="dl1">Top item 1</label>
       <ul class="c-accordion__mid">
         <li><span class="o-title">Mid item 1</span></li>
         <li class="has-descendants">
           <input type="checkbox" id="descendants-2" aria-labelledby="dl2">
           <label for="descendants-2" id="dl2" class="o-title">Mid item 1</label>
           <ul class="c-accordion__bottom">
             <li><span>
               <strong>Bottom item 1</strong>
               <div>Description</div>
             </span></li>
             <li><span>
               <strong>Bottom item 2</strong>
               <div>Description</div>
             </span></li>
           </ul>
         </li>
         <li><span class="o-title">Mid item 3</span></li>
         <li><span class="o-title">Mid item 4</span></li>
       </ul>
   </ul>
   <a class="c-accordion__toggle"></a>
 </nav>
 ```

 #### Customize

 ```css

  --accordion-top-color: var(--blue-400);
  --accordion-mid-color: var(--blue-100);
  --accordion-bottom-color: var(--blue-50);
  --accordion-hover-color: var(--blue-800);
  --accordion-text-color: var(--grey-800);
  --accordion-toggle-color: var(--grey-500);
  ```
 */

:root {
  --accordion-top-color: var(--blue-100);
  --accordion-mid-color: var(--blue-50);
  --accordion-bottom-color: var(--blue-50);
  --accordion-hover-color: var(--blue-800);
  --accordion-text-color: var(--grey-800);
  --accordion-toggle-color: var(--grey-500);
}

@define-mixin accordion {
  @extend %reset-box;
  color: var(--accordion-text-color);

  &__top {

    > li {

      & > label {
        background-color: var(--accordion-top-color);
        text-transform: uppercase;
      }

      > a:not([class]), label {
        font-weight: bold;
      }
    }
  }

  &__mid {
    background-color: var(--accordion-mid-color);

    > li {
      border-bottom: 1px solid var(--grey-300);
      padding: .5em 1.5em;
      position: relative;
    }

    > li > a:not([class]) {
      font-size: .85rem;
      font-weight: bold;
      padding: .25rem 1.5rem;
      text-transform: uppercase;

    }
  }

  &__bottom {
    background-color: var(--accordion-bottom-color);
  }

  ul {
    @extend %reset-box;

    ul {
      @extend %fade-out;
      position: absolute;
    }

    li {
      position: relative;
    }
  }

  li:not(:last-child) a { border-bottom: var(--grey-300) 1px solid; }

  li {
    &.is-active,
    &:hover,
    &:focus {
      background-color: var(--blue-100);
      box-shadow: inset 0 0 4px rgba(0, 0, 0, .5);
    }

    & > a:not([class]),
    & > label {
      color: var(--accordion-text-color);
      display: block;
      margin-bottom: 0 !important;
      transition: background-color var(--global-duration) ease-out, color var(--global-duration) ease-out;
      user-select: none;

      &:hover,
      &:focus { text-decoration: none; }
    }

    & > input[type='checkbox'] {
      opacity: 0;
      position: absolute;

      &:checked {
        + label + ul,
        + label:nth-of-type(n) + ul {
          @extend %fade-in;
          position: relative;
        }
      }
    }

    & > input[type='checkbox'] {

      &:checked {

        + label {
          &::before { transform: rotate(90deg); }
        }
      }

      + label {
        &::before {
          @mixin triangle right, var(--grey-700), 5px;
          cursor: pointer;
          left: 10px;
          position: absolute;
          top: 13px;
          transition: transform var(--global-duration) ease-out, border var(--global-duration) ease-out;
        }
      }
    }
  }

  li > label,
  a {
    display: block;
    padding: .5rem 1.5rem;
    position: relative;
  }

  label { cursor: pointer; }

  li > strong {
    font-size: .85rem;
    text-transform: uppercase;
  }
}

.c-accordion,
%accordion {
  @mixin accordion;
  .o-title {
    font-size: 14px;
    font-weight: bold;
    line-height: 1.5;
    margin: 0 !important;
    text-transform: uppercase;
  }
}

.c-accordion--left,
.c-accordion--right {
  .c-accordion__toggle {
    background-color: var(--accordion-toggle-color);
    display: block;
    height: 24px;
    position: relative;

    &::after {
      position: absolute;
      top: 7px;
    }
  }
}

.c-accordion--left {
  @extend %accordion;

  .c-accordion__toggle {
    border-radius: 0 0 var(--global-rounded) 0;

    &::after {
      @mixin triangle left, var(--white), 5px;
      right: 10px;
    }
  }
}

.c-accordion--right {
  @extend %accordion;

  .c-accordion__toggle {
    border-radius: 0 0 0 var(--global-rounded);

    &::after {
      @mixin triangle right, var(--white), 5px;
      left: 10px;
    }
  }

}

.o-icon-bar {
  @extend %fade-out;
  background-color: var(--blue-100);
  border-bottom-left-radius: 4px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, .5);
  position: absolute;
  right: 0;
  top: 1px;

  > button {
    cursor: pointer;
    font-size: 20px;
    min-width: 37px;
  }

  li .is-active &,
  li .has-descendants:hover &,
  li .has-descendants:focus & {
    @extend %fade-in;
  }
}

