/*---
 section: Navigation
 title: Subnav
 ---

 A horizontal list of options using a `<ul>`.

 #### Horizontal

 ```example:html
 <ul class="c-subnav--horizontal">
   <li><a href="#" class="c-subnav--active">Link 1</a></li>
   <li><a href="#">Link 2</a></li>
   <li><a href="#">Link 3</a></li>
 </ul>
 ```

 #### Vertical

 ```example:html
 <ul class="c-subnav--vertical">
   <li><a href="#" class="c-subnav--active">Link 1</a></li>
   <li><a href="#">Link 2</a></li>
   <li><a href="#">Link 3</a></li>
 </ul>
 ```
 */

@define-mixin subnav-active {
  border-bottom: 3px solid var(--blue-800) !important;
  color: var(--blue-800);
}

@define-mixin subnav {
  @extend %no-bullets;

  a,
  button {
    border-bottom: 3px solid transparent;
    font-size: 12px;
    letter-spacing: 1px;
    padding: .5rem 1rem;
    text-transform: uppercase;
    transition-duration: var(--global-duration);

    &:not([class]) { color: var(--grey-700); }

    &:hover,
    &:focus {
      border-bottom: 3px solid var(--blue-400);
      text-decoration: none !important;
    }

    &:active { @mixin subnav-active; }
  }
}

@define-mixin subnav-inline {
  @extend %list-horizontal;
  @mixin subnav;
  @mixin list-inline 0;
}

.c-subnav {
  @mixin subnav;

  &--active { @mixin subnav-active; }

  &--horizontal {
    @mixin subnav-inline;
  }

  &--vertical {
    @mixin subnav;

    a { display: inline-block; }
  }
}

