/*---
 title: Menu
 section: Navigation
 ---

 ```example:html
 <div class="c-menu is-active">
   <ul>
     <li><a href="#">Thing 1</a></li>
     <li><a href="#">Thing 2</a></li>
   </ul>
 </div>
 ```

 */

@define-mixin menu $background, $hover {
  > ul {
    @extend %row;
    @extend %reset-box;
    background-color: $background;
    color: contrast($background);
    position: relative;

    > li > a:not([class]) {
      color: contrast($background);
      cursor: pointer;
      display: block;
      font-size: 18px;
      padding: .7rem 1.5rem;
      transition-duration: var(--global-duration);

      + ul {
        background-color: $background;
        color: contrast($background);
        min-width: 150px;

        a:not([class]) {
          color: contrast($background);
          display: block;
          padding: .7rem 1rem;
          width: 100%;

          &:hover,
          &:focus {
            text-decoration: none;
          }
        }
      }
    }

    > li.is-active > a,
    > li:hover > a,
    > li:focus > a {
      background-color: $hover;
      color: var(--gold-300);
      text-decoration: none;
      + ul {
        display: block;
        a:hover,
        a:focus {
          background-color: $hover;
        }
      }
    }
  }
}

.c-menu,
%menu { @mixin menu var(--primary-color), var(--blue-700); }

.c-menu {
  ul { display: none; }
  &.is-active ul { display: block !important; }
}

