/* stylelint-disable rule-properties-order */

:root {
  --gutter-width: var(--global-gutter-width);
  --outter-margin: var(--global-outter-margin);
  --gutter-compensation: calc((var(--gutter-width) * .5) * -1);
  --half-gutter-width: calc((var(--gutter-width) * .5));
  --sm-min: var(--global-small-media-min);
  --md-min: var(--global-medium-media-min);
  --lg-min: var(--global-large-media-min);
  --container-sm: calc(var(--sm-min) + var(--gutter-width));
  --container-md: calc(var(--md-min) + var(--gutter-width));
  --container-lg: calc(var(--lg-min) + var(--gutter-width));
  --col: calc(100% / 12);
  --col-1: var(--col);
  --col-2: calc(var(--col) * 2);
  --col-3: calc(var(--col) * 3);
  --col-4: calc(var(--col) * 4);
  --col-5: calc(var(--col) * 5);
  --col-6: calc(var(--col) * 6);
  --col-7: calc(var(--col) * 7);
  --col-8: calc(var(--col) * 8);
  --col-9: calc(var(--col) * 9);
  --col-10: calc(var(--col) * 10);
  --col-11: calc(var(--col) * 11);
  --col-12: calc(var(--col) * 12);
}
/* stylelint-enable */

%block { display: block; }

%reset-box {
  border: 0 !important;
  margin: 0 !important;
  padding: 0 !important;
}

%reset-mobile-order {
  @media var(--sm) {
    order: 0 !important;
  }
}

%spacer { flex: auto; }

/* Layout */

.u-layout,
%layout {
  display: flex;
  flex-direction: column;
}

/* Containers */

.u-container,
%container {
  margin-left: auto;
  margin-right: auto;
}

.u-container--flex,
%container-flex {
  @extend %reset-box;
  align-items: center;
  display: flex;
  height: 100%;
  justify-content: center;
}

.u-container--fluid,
%container-fluid {
  @extend %container;
  padding-left: var(--outter-margin);
  padding-right: var(--outter-margin);
}

/* Row */

@define-mixin row {
  box-sizing: border-box;
  display: flex;
  flex: 0 1 auto;
  flex-direction: row;
  flex-wrap: wrap;
  margin-left: var(--gutter-compensation);
  margin-right: var(--gutter-compensation);
}

.u-row,
%row { @mixin row; }

.u-row--reverse,
%row-reverse { flex-direction: row-reverse; }

.u-first,
.u-column--first,
%first { order: -1; }

.u-last,
.u-column--last,
%last { order: 1; }

.u-row--break,
%row-break {
  margin-left: calc(-1rem - 1px);
  margin-right: calc(-1rem - 1px);
}

.u-row--collapse > [class*='u-column'] {
  padding-left: 0;
  padding-right: 0;
}

/* Columns */

@define-mixin column-pattern {
  box-sizing: border-box;
  flex: 1 0 0%;
  max-width: 100%;
  padding-left: var(--half-gutter-width);
  padding-right: var(--half-gutter-width);
}

@define-mixin column $width {
  flex-basis: $width !important; /* IE11 */
  max-width: $width !important;
  @mixin column-pattern;
}

@define-mixin push $width {
  margin-left: var(--col-$(width));
}

.u-column { @mixin column-pattern; }
.u-column--reverse { flex-direction: column-reverse; }

.u-column--1 { @mixin column var(--col-1); }
.u-column--2 { @mixin column var(--col-2); }
.u-column--3 { @mixin column var(--col-3); }
.u-column--4 { @mixin column var(--col-4); }
.u-column--5 { @mixin column var(--col-5); }
.u-column--6 { @mixin column var(--col-6); }
.u-column--7 { @mixin column var(--col-7); }
.u-column--8 { @mixin column var(--col-8); }
.u-column--9 { @mixin column var(--col-9); }
.u-column--10 { @mixin column var(--col-10); }
.u-column--11 { @mixin column var(--col-11); }
.u-column--12 { @mixin column var(--col-12); }

.u-push--1 { @mixin push 1; }
.u-push--2 { margin-left: var(--col-2); }
.u-push--3 { margin-left: var(--col-3); }
.u-push--4 { margin-left: var(--col-4); }
.u-push--5 { margin-left: var(--col-5); }
.u-push--6 { margin-left: var(--col-6); }
.u-push--7 { margin-left: var(--col-7); }
.u-push--8 { margin-left: var(--col-8); }
.u-push--9 { margin-left: var(--col-9); }
.u-push--10 { margin-left: var(--col-10); }
.u-push--11 { margin-left: var(--col-11); }

.u-pull--1 { margin-right: var(--col-1); }
.u-pull--2 { margin-right: var(--col-2); }
.u-pull--3 { margin-right: var(--col-3); }
.u-pull--4 { margin-right: var(--col-4); }
.u-pull--5 { margin-right: var(--col-5); }
.u-pull--6 { margin-right: var(--col-6); }
.u-pull--7 { margin-right: var(--col-7); }
.u-pull--8 { margin-right: var(--col-8); }
.u-pull--9 { margin-right: var(--col-9); }
.u-pull--10 { margin-right: var(--col-10); }
.u-pull--11 { margin-right: var(--col-11); }

/* Grid Utilities */

.u-start,
%start {
  justify-content: flex-start;
  text-align: start;
}

.u-center,
%center {
  justify-content: center;
  text-align: center;
}

.u-end,
%end {
  justify-content: flex-end;
  text-align: end;
}

@define-mixin text-align $x, $y {
  align-items: $y;
  display: flex;
  justify-content: $x;
}

.u-text-center,
%text-center { @mixin text-align center, center; }

.u-text-middle,
%text-middle { @mixin text-align flex-start, center; }

.u-top,
%top { align-items: flex-start; }

.u-middle,
%middle { align-items: center; }

.u-bottom,
%bottom { align-items: flex-end; }

.u-around,
%around { justify-content: space-around; }

.u-between,
%between { justify-content: space-between; }

