/*---
 title: Layout
 section: Elements
 ---

 The main layout depends on the `.s-app` class to render a holy grail layout that scales down nicely to mobile.

 <div class="u-column--6@md">
 <img src="dist/img/layout-holy-grail.svg" alt="">
 </div>

 [Mozilla - Holy Grail Layout](https://developer.mozilla.org/en-US/docs/Web/CSS/CSS_Flexible_Box_Layout/Using_CSS_flexible_boxes#Holy_Grail_Layout_example)
 */

/* Reset */

::before,
::after {
  text-decoration: inherit;
  vertical-align: inherit;
}

/* specify the border style and width of all elements */

*,
::before,
::after {
  border-style: solid;
  border-width: 0;
  box-sizing: inherit;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
}

/* specify the core styles of all elements */

* {
  background-repeat: no-repeat;
  font-size: inherit;
  line-height: inherit;
  margin: 0;
  padding: 0;
}

.t-default {
  html {
    -ms-overflow-style: -ms-autohiding-scrollbar; /* Edge 12+, Internet Explorer 11- */
    overflow-y: scroll; /* All browsers without overlaying scrollbars */
    text-size-adjust: 100%; /* iOS 8+ */
  }

  details {
    @extend %block; /* Edge 12+, Firefox 40+, Internet Explorer 11-, Windows Phone 8.1+ */
  }

  progress {
    display: inline-block; /* Internet Explorer 11-, Windows Phone 8.1+ */
  }

  summary {
    @extend %block; /* Firefox 40+, Internet Explorer 11-, Windows Phone 8.1+ */
  }

  audio:not([controls]) {
    @extend %hide; /* Chrome 44-, iOS 8+, Safari 9+ */
  }

  pre {
    overflow: auto; /* Internet Explorer 11- */
  }

  /* stylelint-disable selector-root-no-composition, root-no-standard-properties */
  svg:not(:root) {
    overflow: hidden; /* Internet Explorer 11- */
  }
  /* stylelint-enable */

  template {
    @extend %hide; /* Android 4.3-, Internet Explorer 11-, iOS 7-, Safari 7-, Windows Phone 8.1+ */
  }

  textarea {
    overflow: auto; /* Edge 12+, Internet Explorer 11- */
  }

  /* specify the alignment of media elements */
  audio,
  canvas,
  iframe,
  img,
  svg,
  video { vertical-align: middle; }
}

/* Scope Application Styles */

.s-app {

  body {
    @extend %layout;
    border: 0;
    margin-top: 58px; /* combined height of .o-topbar */
    padding: 0;
  }

  &,
  body,
  #app,
  .s-app-container,
  .s-app-container > .u-row {
    height: 100%;
  }
}

.t-default {
  background-color: var(--background-color);
  box-sizing: border-box;
  color: var(--body-copy);
  margin: 0;
  overflow-x: hidden;
  width: 100%;
}

.o-header {
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 200;
}

.o-main {
  @extend %container-fluid;
  @extend %row;
  @extend %row-break;
  height: calc(100% - 58px);
  min-height: 500px;
  @media (--sm-down) { flex-direction: column; }

  > article {
    @extend %reset-mobile-order;
    flex: 2 1 80%;
    order: 2;
  }

  > nav {
    @extend %reset-mobile-order;
    flex: 1 6 20%;
    order: 1;
    @media (--sm-down) {
      max-height: 50px;
      min-height: 50px;
    }
  }
}

