/*---
 section: Patterns
 title: Card
 ---

 ```example:html
 <div class="c-card">
   <h3>Card Title</h3>
   <p>I could have used lorem ipsum, but what's the fun in that?</p>
 </div>
 ```

 ```example:html
 <div class="c-card--transparent">
   <h3>Card Transparent</h3>
   <p>I could have used lorem ipsum, but what's the fun in that?</p>
 </div>
 ```

 ```example:html
 <div class="c-card--list">
   <h3>Card List</h3>
   <p>I could have used lorem ipsum, but what's the fun in that?</p>
 </div>
 ```
 */

@import 'stylep-card';

@define-mixin card $background: var(--white), $shadow: 0 0 10px rgba(0, 0, 0, .4) {
  @mixin card-block;
  @mixin card-solid $background, #444, $shadow;
  padding: .5rem 1rem;

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 { margin-top: 10px; }
}

@define-mixin card-list {
  @mixin card-block;
  @mixin card-solid var(--white), #444, 0 1px 4px rgba(0, 0, 0, .4);
  margin-bottom: 0;
}

.c-card {
  @mixin card;

  &--transparent {
    @mixin card transparent;
    box-shadow: none !important;
  }

  &--list { @mixin card-list; }
}
